import uniCollapse from '@/components/uni-collapse/uni-collapse.vue';
import uniCollapseItem from '@/components/uni-collapse-item/uni-collapse-item.vue';  
import { indexinfo } from '@/static/js/api.js'
export default {
    components: {uniCollapse,uniCollapseItem}, 
	data(){
		return{
			resdetail:{
				amtPaid: 0,
				carCount: 0,
				eventCount: 0
			},
			indexstatus:{
				more:false,
				text:"",
				img:"../../static/image/pullmore.png",
				addstatus:true
			}
		}
	},
	onShow(){
		indexinfo({}).then((res)=>{
			if(res.code === "200"){
				this.resdetail.amtPaid = res.data.amtPaid
				this.resdetail.carCount = res.data.carCount
				this.resdetail.eventCount = res.data.eventCount
			}
			
		})
	},
	methods:{
		download(){
			uni.navigateTo({
				url:"/pages/index/download/download"
			})
		},
		gotoadd(){
			if(this.indexstatus.addstatus){
				this.indexstatus.addstatus = false
				console.log(1)
				if(uni.getStorageSync('token')||uni.getStorageSync('token')!==""){
					let serverstatus = parseInt(uni.getStorageSync('status'))
					if(serverstatus && serverstatus !=="" && serverstatus !== null){
						// 跳转绑定车辆
						uni.navigateTo({
							url:"/pages/carinfo/index"
						})
					}else{
						// 跳转添加车辆
						uni.navigateTo({
							url:"/pages/addcar/index"
						})
					}
				}else{
					// 登录
					uni.navigateTo({
						url:"/pages/login/index"
					})
				}
				this.indexstatus.addstatus = true
			}else{
				return
			}
			
			
		},
		clickmore(){
			this.indexstatus.more = !this.indexstatus.more
			this.indexstatus.more?this.indexstatus.img ="../../static/image/pullcallback.png":this.indexstatus.img ="../../static/image/pullmore.png"
		}
	}
}
